.slideshow-container{
    width: 100%;
    max-height: 850px;
    background-color: aqua;
    .controls {
        display: flex;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        z-index: 10;
        width: 100%;
        justify-content: space-between;
        .prev, .next{
          list-style: none;
          width: 40px;
          height: 40px;
          background-color: var(--text-color);
          border-radius:50%; 
          display: flex;
          justify-content: center;
          align-items: center;
          font-size: 30px; 
          color: var(--white-color);
          &:hover{
            background-color: #10317887;
          }    
        }
    }
    .my-slider{
        width: 100%;
        max-height: 850px;
        .slider-item{
            width: 100%;
            height: 100%;
            position: relative;
            img{
                width: 100%;
                height: 100%;
            }
            .slider-item__caption{
                position: absolute;
                left: 50%;
                top: 50%;
                .slider-item__caption-content{
                    p{

                    }
                }
                .slider-item__caption-heading{
                    h1{

                    }
                }
                .slider-item__caption-button{
                    display: flex;
                    span{

                    }
                    i{

                    }
                }
            }
        }
    }
}
 
 




 